<template>
  <v-card style="background-color: transparent" elevation="0" :disabled="loading">
    <v-row >
      <v-col class="d-flex flex-wrap py-0" cols="12">
        <v-btn v-if="$store.state.permissions && $store.state.permissions['create-post']" :to="{name: 'DashBlogAdd'}" class="dash-btns dash-btns--blue mb-2 mx-2" height="32" dark >

          <v-icon class="ml-1" small >mdi-plus</v-icon>
          پست جدید

        </v-btn>

        <v-btn :to="{name: 'DashBlogCategory'}" class="dash-btns dash-btns--blue mb-2 mx-2" height="32" dark >

          <v-icon small class="ml-1" >mdi-file-tree</v-icon>
          دسته بندی ها

        </v-btn>
      </v-col>
      <v-col md="12" cols="12">
        <BlockItem>
          <span slot="title">مطالب وبلاگ</span>
          <template slot="icon">mdi-account-multiple</template>
          <v-data-table
              hide-default-footer
              :headers="headers" :loading="loading" :items="data.data" item-key="id" disable-sort
          >

            <template v-slot:[`item.title`]="{item}">
                <a class="secondary--text" @click="toPostView(item.id)" v-if="item.title.length>50">{{item.title.substring(0,50) + '...'}}</a>
                <a class="secondary--text" @click="toPostView(item.id)" v-else>{{item.title}}</a>
            </template>

            <template v-slot:[`item.category`]="{item}">
              <div style="font-size: 12px" v-for="(cat,i) in item.category" :key="i+'catFor'">
              {{cat.title}} <span v-if="i!==(item.category.length-1)">, </span>
              </div>
            </template>

            <template v-slot:[`item.enabled`]="{item}">
              <v-btn @click="changeStatus(item.id,item.enabled,'enabled')" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn>
              <span class="table-flag-enabled table-flag-enabled--green" v-if="item.enabled" >فعال</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else>غیر فعال</span>
            </template>

            <template v-slot:[`item.featured`]="{item}">
              <v-btn @click="changeStatus(item.id,item.featured,'featured')" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn>
              <span class="table-flag-enabled table-flag-enabled--green" v-if="item.featured" >فعال</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else >غیر فعال</span>
            </template>

            <template v-slot:[`item.comments_enabled`]="{item}">
              <v-btn @click="changeStatus(item.id,item.comments_enabled,'comments_enabled')" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn>
              <span class="table-flag-enabled table-flag-enabled--green" v-if="item.comments_enabled" >فعال</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else >غیر فعال</span>
            </template>

            <template v-slot:[`item.date_created_date`]="{item}">
              <span style="font-size: 12px" >{{item.date_created_date | momentHour}}</span>
            </template>

            <template v-slot:[`item.date_published_date`]="{item}">
              <span style="font-size: 12px" >{{item.date_created_date | momentHour}}</span>
            </template>

            <template v-slot:[`item.date_updated_date`]="{item}">
              <span style="font-size: 12px" >{{item.date_created_date | momentHour}}</span>
            </template>

            <template v-slot:[`item.author.first_name`]="{item}">
              <span v-if="(item && item.author && item.author.first_name) || (item && item.author && item.author.last_name)" >
              <span style="font-size: 12px" v-if="item.author.first_name" >{{item.author.first_name}} </span>
              <span style="font-size: 12px" v-if="item.author.last_name" >{{item.author.last_name}}</span>
              </span>
              <span v-else >{{item.author.username}}</span>
            </template>



            <template v-slot:[`item.id`]="{item}">
              <div class="d-flex justify-center">
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['change-post']" :to="{name:'DashBlogAdd',params:{id:item.id}}" color="#17a2b8" icon="mdi-pencil" alt="ویرایش پست" />
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['delete-post']" @clickBtn="toDelete(item.id,'/blog/api/v1/post/delete-post/')" color="#DC3545" icon="mdi-delete" alt="حذف پست" />
              </div>
            </template>


          </v-data-table>



          <v-pagination
              v-if="data.total_objects"
              :value="options.page"
              v-model="options.page"
              color="info"
              class="mt-6"
              :length="parseInt(data.total_pages)"
          ></v-pagination>
          <p class="text-center  mb-2 mt-2 info--text" v-if="data.total_objects" style="font-size: 14px">
            {{ data.total_objects }} مطلب</p>


        </BlockItem>
      </v-col>
    </v-row>
    <DeleteDialog :ddata="deleteData" @deleteFn="fetchPosts()" ></DeleteDialog>
    <PostView :cdata="postData" ></PostView>
  </v-card>
</template>

<script>
import axios from "axios";
import BlockItem from "@/components/Misc/BlockItem";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import PostView from "@/components/Dashboard/Misc/PostView";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";

export default {
  name: "DashBlog",
  components : {
    ASbtn,
    PostView,
    DeleteDialog,
    BlockItem
  },
  created() {
    this.checkQueries();
    this.fetchPosts();
  },
  watch: {
    'options.page'() {
      this.fetchPosts();
    }
  },
  data () {
    return {
      options : {
        page : 1,
        ordering: '-id'
      },
      loading : false,
      data: {
        data : []
      },
      deleteData: {
        api : '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      postData: {
        dialog: false,
        loading: false,
        idSelected: null,
      },
    }
  },
  computed : {
    headers() {
      return [
        {
          text: 'عنوان پست',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'نویسنده', class: 'header-table-gray',
          align: 'center',
          value: 'author.first_name',
        },
        {
          text: 'فعال', class: 'header-table-gray',
          align: 'center',
          value: 'enabled',
        },{
          text: 'ویژه', class: 'header-table-gray',
          align: 'center',
          value: 'featured',
        },{
          text: 'کامنت', class: 'header-table-gray',
          align: 'center',
          value: 'comments_enabled',
        },{
          text: 'ایجاد', class: 'header-table-gray',
          align: 'center',
          value: 'date_created_date',
        },{
          text: 'انتشار', class: 'header-table-gray',
          align: 'center',
          value: 'date_published_date',
        },{
          text: 'ویرایش', class: 'header-table-gray',
          align: 'center',
          value: 'date_updated_date',
        },{
          text: 'دسته', class: 'header-table-gray',
          align: 'center',
          value: 'category',
        },{
          text: 'بازدید', class: 'header-table-gray',
          align: 'center',
          value: 'views',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'id',
        },


      ]
    },
  },
  methods : {
    changeStatus (id,enabled,string) {
      enabled = !enabled;
      axios.patch(this.$store.state.host + '/blog/api/v1/post/change-post/'+id,{
        [string] : enabled,
      },{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(()=>{
        this.fetchPosts();
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    toPostView(id) {
      this.postData.idSelected = id;
      this.postData.dialog = true;
    },
    toDelete(id,api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },
    checkQueries() {
      if(this.$route.query.page){
        this.options.page = parseInt(this.$route.query.page);
      }

      if(this.$route.params.id) {
        this.options.category = this.$route.params.id;
      }

      if(this.$route.query.search) {
        this.options.search = this.$route.query.search;
      }

      if(this.$route.query.ordering) {
        this.options.ordering = this.$route.query.ordering;
      }

      if(this.$route.query.tag) {
        this.options.tag = this.$route.query.tag;
      }
    },

    fetchPosts () {
      this.loading = true;
      axios.get(this.$store.state.blogApi + 'post/posts',{
        params : this.options
      }).catch(error=>{
        this.errorHandle(error.response)
      }).then(response=>{
        this.data = response.data;
        this.loading = false;
      })
    }
  }
}
</script>

